import React from 'react';
import { Helmet } from 'react-helmet';

// Sees da pgina Home
import HeroSection from '../components/sections/HeroSection';
import AboutPreview from '../components/sections/AboutPreview';
import ProjectsPreview from '../components/sections/ProjectsPreview';

// Configuraes
import { seoConfig } from '../config/portfolio';

/**
 * Home - Pgina inicial do portflio
 *
 * Agrega sees principais com overview das habilidades e projetos.
 *
 * Funcionalidades:
 * - Carrega heri, destaques de projetos e experincia
 * - Metadados para SEO na pgina inicial
 * - Integrao com sees reutilizveis
 *
 * @component
 * @example
 * return (
 *   <Home />
 * )
 */
const Home = () => {
  return (
    <>
      <Helmet>
        <title>{seoConfig.title}</title>
        <meta name="description" content={seoConfig.description} />
        <meta property="og:title" content={seoConfig.ogTitle} />
        <meta property="og:description" content={seoConfig.ogDescription} />
        <meta property="og:type" content="website" />
        <meta property="og:url" content={seoConfig.url} />
        <meta property="og:image" content={seoConfig.image} />
        
        <meta name="twitter:card" content={seoConfig.twitterCard} />
        <meta name="twitter:title" content={seoConfig.ogTitle} />
        <meta name="twitter:description" content={seoConfig.ogDescription} />
        <meta name="twitter:image" content={seoConfig.image} />
      </Helmet>

      {/* Pgina Home principal */}
      <HeroSection />
      <AboutPreview />
      <ProjectsPreview />
    </>
  );
};

export default Home;
