import React from 'react';
import { IconButton, Tooltip, Box } from '@mui/material';
import { Brightness4, Brightness7 } from '@mui/icons-material';
import { useTheme } from '../../contexts/ThemeContext';

/**
 * ThemeToggle - Boto para alternar entre modo claro e escuro
 * 
 * Exibe cone de sol (modo claro) ou lua (modo escuro) com tooltip explicativo.
 * Possui animao suave na transio e feedback visual ao clicar.
 * 
 * @component
 * @example
 * return (
 *   <ThemeToggle />
 * )
 */
const ThemeToggle = () => {
  const { darkMode, toggleTheme } = useTheme();

  return (
    <Tooltip 
      title={darkMode ? 'Alternar para modo claro' : 'Alternar para modo escuro'}
      arrow
    >
      <Box
        component="div"
        sx={{
          position: 'relative',
          display: 'inline-block',
        }}
      >
        <IconButton
          onClick={toggleTheme}
          color="inherit"
          sx={{
            transition: 'all 0.3s ease-in-out',
            transform: 'scale(1)',
            color: darkMode ? '#fff' : '#374151', // Cor base do boto
            '&:hover': {
              transform: 'scale(1.1)',
              backgroundColor: darkMode 
                ? 'rgba(255, 255, 255, 0.08)' 
                : 'rgba(59, 130, 246, 0.08)', // Azul suave no modo claro
              color: darkMode ? '#64b5f6' : '#3b82f6', // Hover colors
            },
            '&:active': {
              transform: 'scale(0.95)',
            },
            // Efeito de destaque para indicar funcionalidade
            position: 'relative',
            '&::before': {
              content: '""',
              position: 'absolute',
              top: '50%',
              left: '50%',
              transform: 'translate(-50%, -50%)',
              width: '100%',
              height: '100%',
              borderRadius: '50%',
              background: darkMode 
                ? 'linear-gradient(45deg, #4dd0e1 30%, #64b5f6 90%)'
                : 'linear-gradient(45deg, #1976d2 30%, #00bcd4 90%)',
              opacity: 0,
              transition: 'opacity 0.3s ease-in-out',
              zIndex: -1,
            },
            '&:hover::before': {
              opacity: 0.1,
            },
          }}
          aria-label={darkMode ? 'Alternar para modo claro' : 'Alternar para modo escuro'}
        >
          {/* cone com animao de rotao suave */}
          <Box
            sx={{
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center',
              transition: 'transform 0.5s ease-in-out',
              transform: darkMode ? 'rotate(180deg)' : 'rotate(0deg)',
            }}
          >
            {darkMode ? (
              <Brightness7 
                sx={{ 
                  fontSize: '1.5rem',
                  color: 'inherit', // Herda a cor do IconButton
                  filter: 'drop-shadow(0 0 8px rgba(255, 255, 255, 0.3))',
                  transition: 'color 0.3s ease-in-out',
                }} 
              />
            ) : (
              <Brightness4 
                sx={{ 
                  fontSize: '1.5rem',
                  color: 'inherit', // Herda a cor do IconButton
                  filter: 'drop-shadow(0 0 8px rgba(0, 0, 0, 0.2))',
                  transition: 'color 0.3s ease-in-out',
                }} 
              />
            )}
          </Box>
        </IconButton>
        
        {/* Indicador visual sutil do estado atual */}
        <Box
          sx={{
            position: 'absolute',
            bottom: 2,
            right: 2,
            width: 6,
            height: 6,
            borderRadius: '50%',
            backgroundColor: darkMode ? '#4dd0e1' : '#1976d2',
            opacity: 0.8,
            transition: 'all 0.3s ease-in-out',
            transform: 'scale(1)',
            animation: 'pulse 2s infinite',
            '@keyframes pulse': {
              '0%': {
                transform: 'scale(1)',
                opacity: 0.8,
              },
              '50%': {
                transform: 'scale(1.2)',
                opacity: 0.4,
              },
              '100%': {
                transform: 'scale(1)',
                opacity: 0.8,
              },
            },
          }}
        />
      </Box>
    </Tooltip>
  );
};

export default ThemeToggle;
