/**
 * ThemeContext - Sistema de temas aprimorado para o portflio
 * 
 * Caractersticas:
 * - Suporte a tema claro e escuro
 * - Configurao centralizada de cores e tipografia
 * - Componentes Material-UI customizados
 * - Persistncia do tema selecionado
 * - Transies suaves entre temas
 */

import React, { createContext, useContext, useState, useEffect } from 'react';
import { createTheme, ThemeProvider as MuiThemeProvider } from '@mui/material/styles';
import PropTypes from 'prop-types';
import { themeConfig } from '../config/portfolio';

// Criar contexto do tema
const ThemeContext = createContext();

// Hook para usar o contexto do tema
export const useTheme = () => {
  const context = useContext(ThemeContext);
  if (!context) {
    throw new Error('useTheme deve ser usado dentro de um ThemeProvider');
  }
  return context;
};

// Funo para criar tema base
const createBaseTheme = (mode) => {
  const isDark = mode === 'dark';
  const colors = isDark ? themeConfig.dark : themeConfig.light;
  
  return createTheme({
    palette: {
      mode,
      primary: {
        main: colors.primaryColor,
        light: isDark ? '#90caf9' : '#5e92f3',
        dark: isDark ? '#1976d2' : '#1565c0',
        contrastText: '#ffffff',
      },
      secondary: {
        main: colors.secondaryColor,
        light: isDark ? '#ff7961' : '#ff6659',
        dark: isDark ? '#c62828' : '#d32f2f',
        contrastText: '#ffffff',
      },
      background: {
        default: colors.backgroundColor,
        paper: colors.paperColor,
      },
      text: {
        primary: colors.textPrimary,
        secondary: colors.textSecondary,
      },
      error: {
        main: '#f44336',
      },
      warning: {
        main: '#ff9800',
      },
      info: {
        main: '#2196f3',
      },
      success: {
        main: colors.accentColor,
      },
      divider: colors.dividerColor,
    },
    typography: {
      fontFamily: themeConfig.fontFamily,
      h1: {
        fontSize: themeConfig.fontSize.xxxlarge,
        fontWeight: themeConfig.fontWeight.bold,
        lineHeight: 1.2,
        letterSpacing: '-0.02em',
      },
      h2: {
        fontSize: themeConfig.fontSize.xxlarge,
        fontWeight: themeConfig.fontWeight.semibold,
        lineHeight: 1.3,
        letterSpacing: '-0.01em',
      },
      h3: {
        fontSize: '1.75rem',
        fontWeight: themeConfig.fontWeight.semibold,
        lineHeight: 1.4,
      },
      h4: {
        fontSize: themeConfig.fontSize.large,
        fontWeight: themeConfig.fontWeight.medium,
        lineHeight: 1.4,
      },
      h5: {
        fontSize: '1.125rem',
        fontWeight: themeConfig.fontWeight.medium,
        lineHeight: 1.5,
      },
      h6: {
        fontSize: themeConfig.fontSize.medium,
        fontWeight: themeConfig.fontWeight.medium,
        lineHeight: 1.6,
      },
      body1: {
        fontSize: themeConfig.fontSize.medium,
        lineHeight: 1.7,
      },
      body2: {
        fontSize: themeConfig.fontSize.small,
        lineHeight: 1.6,
      },
      subtitle1: {
        fontSize: '1.125rem',
        fontWeight: themeConfig.fontWeight.regular,
        lineHeight: 1.5,
      },
      subtitle2: {
        fontSize: themeConfig.fontSize.small,
        fontWeight: themeConfig.fontWeight.medium,
        lineHeight: 1.4,
        textTransform: 'uppercase',
        letterSpacing: '0.5px',
      },
      button: {
        fontSize: themeConfig.fontSize.small,
        fontWeight: themeConfig.fontWeight.medium,
        textTransform: 'none',
        letterSpacing: '0.3px',
      },
      caption: {
        fontSize: '0.75rem',
        lineHeight: 1.4,
      },
    },
    shape: {
      borderRadius: parseInt(themeConfig.borderRadius.medium),
    },
    spacing: 8, // padro do Material-UI
    transitions: {
      duration: {
        shortest: 150,
        shorter: 200,
        short: 250,
        standard: 300,
        complex: 375,
        enteringScreen: 225,
        leavingScreen: 195,
      },
    },
    components: {
      // Botes customizados
      MuiButton: {
        styleOverrides: {
          root: {
            borderRadius: parseInt(themeConfig.borderRadius.large),
            padding: '12px 32px',
            fontSize: '0.95rem',
            fontWeight: themeConfig.fontWeight.medium,
            textTransform: 'none',
            boxShadow: 'none',
            transition: `all 300ms ease`,
            '&:hover': {
              boxShadow: isDark ? '0 4px 20px rgba(100, 181, 246, 0.3)' : '0 4px 20px rgba(33, 150, 243, 0.3)',
              transform: 'translateY(-2px)',
            },
          },
          containedPrimary: {
            background: `linear-gradient(45deg, ${colors.primaryColor} 30%, ${colors.accentColor} 90%)`,
            color: '#ffffff',
            '&:hover': {
              background: `linear-gradient(45deg, ${colors.secondaryColor} 30%, ${colors.primaryColor} 90%)`,
            },
          },
        },
      },
      // Cards customizados
      MuiCard: {
        styleOverrides: {
          root: {
            borderRadius: parseInt(themeConfig.borderRadius.large),
            border: `1px solid ${colors.dividerColor}`,
            boxShadow: colors.shadow,
            transition: `all 300ms ease`,
            '&:hover': {
              transform: 'translateY(-4px)',
              boxShadow: colors.hoverShadow,
            },
          },
        },
      },
      // Chips customizados
      MuiChip: {
        styleOverrides: {
          root: {
            borderRadius: parseInt(themeConfig.borderRadius.small),
            fontWeight: themeConfig.fontWeight.medium,
          },
          outlined: {
            borderColor: colors.dividerColor,
            '&:hover': {
              backgroundColor: colors.primaryColor + '10',
              borderColor: colors.primaryColor,
            },
          },
        },
      },
      // AppBar customizada
      MuiAppBar: {
        styleOverrides: {
          root: {
            backgroundColor: isDark 
              ? 'rgba(15, 23, 42, 0.95)' 
              : 'rgba(255, 255, 255, 0.95)',
            backdropFilter: 'blur(20px)',
            borderBottom: `1px solid ${colors.dividerColor}`,
          },
        },
      },
      // Paper customizado
      MuiPaper: {
        styleOverrides: {
          root: {
            backgroundImage: 'none',
            backgroundColor: colors.surface,
            border: `1px solid ${colors.border}`,
          },
        },
      },
      // Tabs customizadas
      MuiTabs: {
        styleOverrides: {
          indicator: {
            backgroundColor: colors.primary,
            height: 3,
            borderRadius: '3px 3px 0 0',
          },
        },
      },
      MuiTab: {
        styleOverrides: {
          root: {
            textTransform: 'none',
            fontWeight: 500,
            fontSize: '0.95rem',
            minHeight: 48,
            '&.Mui-selected': {
              color: colors.primary,
            },
          },
        },
      },
      // Lista customizada
      MuiListItem: {
        styleOverrides: {
          root: {
            borderRadius: themeConfig.borderRadius.medium,
            marginBottom: themeConfig.spacing.unit,
            '&:hover': {
              backgroundColor: colors.primary + '10',
            },
            '&.Mui-selected': {
              backgroundColor: colors.primary + '15',
              '&:hover': {
                backgroundColor: colors.primary + '20',
              },
            },
          },
        },
      },
      // TextField customizado
      MuiTextField: {
        styleOverrides: {
          root: {
            '& .MuiOutlinedInput-root': {
              borderRadius: themeConfig.borderRadius.medium,
              '& fieldset': {
                borderColor: colors.border,
              },
              '&:hover fieldset': {
                borderColor: colors.primary,
              },
              '&.Mui-focused fieldset': {
                borderColor: colors.primary,
              },
            },
          },
        },
      },
    },
  });
};

// Componente ThemeProvider
const ThemeProvider = ({ children }) => {
  const [darkMode, setDarkMode] = useState(() => {
    // Verificar preferncia salva ou preferncia do sistema
    const savedTheme = localStorage.getItem('theme');
    if (savedTheme) {
      return savedTheme === 'dark';
    }
    return window.matchMedia('(prefers-color-scheme: dark)').matches;
  });

  // Salvar preferncia do tema
  useEffect(() => {
    localStorage.setItem('theme', darkMode ? 'dark' : 'light');
  }, [darkMode]);

  const toggleTheme = () => {
    setDarkMode(prev => !prev);
  };

  // Criar tema atual
  const theme = createBaseTheme(darkMode ? 'dark' : 'light');

  const contextValue = {
    theme,
    darkMode,
    toggleTheme,
  };

  return (
    <ThemeContext.Provider value={contextValue}>
      <MuiThemeProvider theme={theme}>
        {children}
      </MuiThemeProvider>
    </ThemeContext.Provider>
  );
};

ThemeProvider.propTypes = {
  children: PropTypes.node.isRequired,
};

export { ThemeProvider };
export default ThemeProvider;
